<template>
	<div>
		<ContentHeader title="Setting Klinik" />
		<div class="content-header">
			<div class="container-fluid">
				<div class="card">
	              <div class="card-header">
	                <h3 class="card-title"><div style="#007bff">Setting Data Klinik</div></h3>

	                <div class="card-tools">
	                </div>
	              </div>
	              <div class="card-body" style="display: block;">
	                <form>
	                	<div class="px-lg-2 ">
		                  <div class="form-group">
		                    <label for="nama">Nama Cabang</label>
		                    <input type="text" class="form-control" id="nama" name="nama" v-model="cabang.nama">
		                  </div>
		                </div>
	                	<div class="px-lg-2 ">
		                  <div class="form-group">
		                    <label for="alamat">Alamat</label>
		                    <input type="text" class="form-control" id="alamat" name="alamat" v-model="cabang.alamat">
		                  </div>
		                </div>
	                	<div class="px-lg-2 ">
		                  <div class="form-group">
		                    <label for="telepon">Telepon</label>
		                    <input type="text" class="form-control" id="telepon" name="telepon" v-model="cabang.telepon">
		                  </div>
		                </div>
	                	<div class="px-lg-2 ">
		                  <div class="form-group">
		                    <label for="penanggung_jawab">Penanggung Jawab</label>
		                    <input type="text" class="form-control" id="penanggung_jawab" name="penanggung_jawab" v-model="cabang.penanggung_jawab">
		                  </div>
		                </div>
		                <div class="px-lg-2">
		                	<button type="submit" class="btn btn-primary">Selesai</button>
		                </div>
	                </form>
	              </div>
	              <!-- /.card-body -->
	              <div class="card-footer" style="display: block;">
	                
	              </div>
	              <!-- /.card-footer-->
            	</div>
			</div>
		</div>
	</div>
</template>

<script>
import ContentHeader from '@/components/ContentHeader.vue'

import { computed, ref, onMounted } from 'vue'
import store from '@/store'
import axios from 'axios'

export default{
	components: {
		ContentHeader: ContentHeader
	},
	setup(){
		const user = computed(() => store.getters['auth/user'])

		const cabang = ref([])
		// console.log(cabang)

		const getCabang = async () => {
			let response = await axios.get(`api/cabang/getWhereId/${user.value.cabang_id}`)
			cabang.value = response.data
			console.log(cabang.value.nama)
		}

		onMounted(() => {
			getCabang()
		})

		return {
			cabang,
			getCabang
		}
	}
}
</script>